
import {defineComponent} from "vue";

export default defineComponent( {
  name: "ScafoldBar",
  props: ['title', 'color', 'collapsed'],
  data() {
    return {
      editMode: false,
      newTitle: null,
      isFolded: false
    }
  },
  methods: {
    collapse(isCollapsed: boolean){
      this.isFolded = isCollapsed
    },
    goToViewMode(){
      this.editMode = false
    },
    goToEditMode(){
      this.editMode = true
      this.newTitle = this.title

      this.$nextTick(() => {
        const html = this.$refs.input as HTMLInputElement
        html.focus()
      });

    }
  }
})

